import React, {Component} from 'react';
import PropTypes from 'prop-types';

/**
 * Generic pagination component.
 */
export default class Pager extends Component {
    render() {
        const {id, value, maxValue, minValue, symbols, tempVal, setProps} = this.props;
        
        var from = value[0];
        var to   = value[1];

        if (tempVal) {
            if (tempVal === 'MM'){
                from = minValue
            } else if (tempVal === 'M'){
                from = from-1
            } else if (tempVal === 'PP'){
                to = maxValue
            } else if (tempVal === 'P'){
                to = to+1
            } else if (tempVal.startsWith("F") && 
                isNaN( from = parseInt(tempVal.replace("F", "")) )
               ) {
                from = tempVal.replace("F", "");
            } else if (tempVal.startsWith("T") && 
                isNaN( to = parseInt(tempVal.replace("T", "")) ) 
               ) {
                to = tempVal.replace("T", "");
            } 
        
            if (typeof from === "number" && value[0] != from && from >= 1 && from <= maxValue)
                this.props.setProps({ 'value': [from, value[1]], 'tempVal': null });
            if (typeof to === "number" && value[1] != to && to >= value[0] && to <= maxValue)
                this.props.setProps({ 'value': [value[0], to], 'tempVal': null });
        }
        
        return (
            <div id={id+"-container"} className="form-row">
                <button id={id+"-mm"} className="btn btn-outline-primary me-2" type="button" 
                    onClick={
                        e => setProps({ tempVal: 'MM' })
                    }>
                    <i className="bi bi-chevron-double-left"></i>{symbols[0]}
                </button>
                <button id={id+"-m"} className="btn btn-outline-primary me-2" type="button" 
                    onClick={
                        e => setProps({ tempVal: 'M' })
                    }>
                    <i className="bi bi-chevron-left"></i>{symbols[1]}
                </button>
                <span className="me-2"> From </span>
                <input
                    id={id+"-ip-from"}
                    value={from}
                    onChange={
                        e => setProps({ tempVal: 'F'+e.target.value })
                    }
                    type="number"
                    style={{width: "10ch", display: "inline"}}
                    className={"form-control" + ((from != value[0]) ? ' is-invalid' : '')}
                />
                <span className="me-2"> to </span>
                <input
                    id={id+"-ip-to"}
                    value={to}
                    onChange={
                        e => setProps({ tempVal: 'T'+e.target.value })
                    }
                    type="number"
                    style={{width: "10ch", display: "inline"}}
                    className={"form-control" + ((to != value[1]) ? ' is-invalid' : '')}
                />
                <span className="me-2"> of {maxValue} </span>
                <button id={id+"-p"} className="btn btn-outline-primary me-2" type="button" 
                    onClick={
                        e => setProps({ tempVal: 'P' })
                    }>
                    <i className="bi bi-chevron-right"></i>{symbols[2]}
                </button>
                <button id={id+"-pp"} className="btn btn-outline-primary me-2" type="button" 
                    onClick={
                        e => setProps({ tempVal: 'PP' })
                    }>
                    <i className="bi bi-chevron-double-right"></i>{symbols[3]}
                </button>
                <input
                    id={id}
                    value={value}
                    type="hidden"
                />
            </div>
        );
    }
}

Pager.defaultProps = {
    minValue: 1,
    symbols: ['','','','']
};

Pager.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The value displayed in the input.
     */
    value: PropTypes.arrayOf(PropTypes.number),

    /**
     * The maximum number of elements displayed in the input.
     */
    maxValue: PropTypes.number.isRequired,

    /**
     * The minimum number of elements displayed in the input.
     */
    minValue: PropTypes.number,
    
    /**
     * The text symbols for the buttons.
     */
    symbols: PropTypes.arrayOf(PropTypes.string),

    /**
     * The maximum number of elements displayed in the input.
     */
    tempVal: PropTypes.string,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
